<template>
    <el-container style="height: 500px; border: 1px solid #eee">
        <el-header class="el-icon-s-home" style=" font-size: 40px; background-color:#409EFF; color:azure">
            教考系统</el-header>
        <el-footer>
            <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
                <el-menu-item index="1"><router-link to="/student" tag="span">账号信息</router-link></el-menu-item>
                <el-menu-item index="2"><router-link to="/course" tag="span">课程表</router-link></el-menu-item>
                <el-menu-item index="3"><router-link to="/studentExam" tag="span">参加考试</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/score" tag="span">考试成绩查询</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/courseSelection" tag="span">选课</router-link></el-menu-item>
                <el-menu-item index="5"><router-link to="/studentLogin" tag="span">退出登录</router-link></el-menu-item>
            </el-menu>
        </el-footer>


        <el-main v-model="studentData">
            <span>学生姓名：{{studentData.studentName}}</span><br><br>
            <span>学生学号：{{studentData.studentId}}</span><br><br>
            <span>学生性别：{{studentData.studentSex}}</span><br><br>
            <span>专业：{{studentData.major.majorName}}</span><br><br>
            <span>已修学分：{{studentData.credit}}</span><br><br>
            <span>所处学期：{{studentData.semester}}</span><br><br>
        </el-main>
    </el-container>
</template>

<script>
import axios from 'axios';

export default {
    data() {
        return {
            studentData: [],
            //学号
            studentID: '',

        }
    },
    mounted() {
        const storedID = sessionStorage.getItem('studentID');
        this.studentID=storedID;
        this.getByID();

    },
    methods: {
        //根学号查询学生表
        getByID(){
            axios.get(`/api/student/id/${this.studentID}`).then((result) => {
                this.studentData=result.data.data;
            });

        },
        
    },
}
</script>

<style scoped>
.el-container {
    min-height: 100vh; /* 至少占据整个视口高度 */
    max-height: 1200px; /* 最大高度限制 */
    overflow-y: auto; /* 如果内容超出高度，显示滚动条 */
    display: flex;
    flex-direction: column;
  }
  
  .el-header, .el-footer {
    flex: 0 0 auto; /* 固定高度 */
    background-color: #409EFF;
    color: azure;
    padding: 10px;
  }
  
  .el-main {
    flex: 1 1 auto; /* 自动占据剩余空间 */
    overflow-y: auto; /* 如果内容超出高度，显示滚动条 */
    padding: 20px;
    box-sizing: border-box;
  }
</style>